<template>
  <div v-bind:id="id"></div>
</template>

<script>
import "pepjs";
export default {
  name: "Liquid",
  props: {
    id: String,
  },
  data() {
    return {
      ctx: null,
      width: null,
      height: null,
      circ: null,
      count: null,
    };
  },
  mounted() {
    this.renderLiquid();
  },
  methods: {
    prepareLiquid() {
      var canvas = document.createElement("canvas");
      this.ctx = canvas.getContext("2d");

      (this.width = canvas.width = window.innerWidth),
        (this.height = canvas.height = window.innerHeight);

      window.addEventListener("resize", function() {
        this.width = canvas.width = window.innerWidth;
        this.height = canvas.height = window.innerHeight;
      });

      document.querySelector(`#${this.id}`).appendChild(canvas);

      this.circ = (4 * (Math.sqrt(2) - 1)) / 3;

      this.count = Math.PI;
    },
    drawBezierCircle(cx, cy, r) {
      var c;

      var offsetX = 20 * Math.sin(this.count);
      var offsetY = 15 * Math.cos(this.count * 2);
      r = r / 2;

      this.count += 0.01;

      this.ctx.translate(cx, cy); // translate to centerpoint

      this.ctx.beginPath();

      // top right
      c = this.circ + 0.2 * Math.sin(this.count);
      this.ctx.moveTo(offsetX + 0, offsetY + -r);
      this.ctx.bezierCurveTo(
        offsetX + c * r,
        offsetY + -r,
        offsetX + r,
        offsetY + -c * r,
        offsetX + r,
        offsetY + 0
      );

      // bottom right
      c = this.circ + 0.2 * Math.cos(this.count);
      this.ctx.bezierCurveTo(
        offsetX + r,
        offsetY + c * r,
        offsetX + c * r,
        offsetY + r,
        offsetX + 0,
        offsetY + r
      );

      // bottom left
      c = this.circ + 0.2 * Math.sin(this.count * 2);
      this.ctx.bezierCurveTo(
        offsetX + -c * r,
        offsetY + r,
        offsetX + -r,
        offsetY + c * r,
        offsetX + -r,
        offsetY + 0
      );

      // top left
      c = this.circ + 0.2 * Math.cos(this.count + 1);
      this.ctx.bezierCurveTo(
        offsetX + -r,
        offsetY + -c * r,
        offsetX + -c * r,
        offsetY + -r,
        offsetX + 0,
        offsetY + -r
      );

      this.ctx.fill();
    },
    render() {
      requestAnimationFrame(this.render);

      this.ctx.setTransform(1, 0, 0, 1, 0, 0);
      this.ctx.clearRect(0, 0, this.width, this.height);

      this.drawBezierCircle(this.width / 2, this.height / 2, 300);
    },
    renderLiquid() {
      this.prepareLiquid();
      this.render();
    },
  },
};
</script>
<style scoped>
canvas {
  display: block;
}
</style>
